@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.compare-header {
  padding: 30px;
}

.switchBTC {
  padding: 10px;
}

.switchETH {
  padding: 10px 10px 30px 10px;
}

.reload-button {
	background-color:#5abeed;
	border-radius:28px;
	border:1px solid #5abeed;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:15px 20px;
	text-decoration:none;
	text-shadow:0px 1px 0px #000000;
}
.reload-button:hover {
	background-color:#0e6ec2;
}
.reload-button:active {
	position:relative;
	top:1px;
}

.coin-container {
    display: flex;
    justify-content: center;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 300px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 200px;
}

.coin h1 {
    font-size: 16px;
    width: 200px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    width: 100px;
    text-transform: uppercase
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price {
    width: 30px;
}

